import {Grid, Box, Card, CardActionArea, CardMedia, Typography} from "@material-ui/core";
import {
    DateInput,
    Edit,
    ImageField,
    ImageInput,
    TextInput,
    TabbedForm,
    FormTab,
    useEditController,
    FileInput,
    FileField,
    useQuery,
    AutocompleteArrayInput,
} from "react-admin";
import RichTextInput from "ra-input-rich-text";
import * as React from "react";
import {makeStyles} from "@material-ui/core/styles";


const ProfileTitle = ({record}) => {
    return <span>Profile {record ? `"${record.first_name}${record.last_name}"` : ''}</span>;
};

const useStyles = makeStyles({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 300,
    },
});


const ShowPicture = ({picture}) => {
    const classes = useStyles();
    return (
        <Box>
            <Typography style={{fontSize: "0.8rem", marginBottom: "10px"}}
                        className="MuiFormLabel-root RaFileInput-root-78">
                Last Picture
            </Typography>
            <Card className={classes.root}>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={picture}
                        title="Contemplative Reptile"
                    />
                </CardActionArea>
            </Card>
        </Box>
    );
}
const ProfileEdit = (props) => {
        const controllerProps = useEditController(props);
        const socialNetwork = useQuery({
            type: 'getList',
            resource: 'socialnetwork',
            payload: {ordering: {}, filter: {}, sort: {}, pagination: {}},
            // payload: {pagination: {page: {1}, perPage: {10}}, sort: {field: {string}, order: {string}}, filter: {Object}}
        });
        console.log(socialNetwork)
        let {
            record, // record fetched via dataProvider.getOne() based on the id from the location
        } = controllerProps;
        if (typeof record == "undefined" || (socialNetwork.loading)) {
            return (
                <React.Fragment/>
            )
        } else {
            const choices = socialNetwork.data.map((value) => {
                return {id: value.id, name: value.title}
            })
            return (

                <Edit
                    title={<ProfileTitle/>}
                    {...props}>
                    <TabbedForm
                        initialValues={{average_note: 0}}
                        // toolbar={<UserEditToolbar/>}
                    >
                        <FormTab label="Page 1">
                            <Grid container spacing={3} style={{width: "100%"}}>
                                <Grid item xs={6}>
                                    <TextInput fullWidth source="first_name"/>
                                    <TextInput fullWidth source="last_name"/>
                                    <DateInput fullWidth source="birth_date"/>

                                </Grid>
                                <Grid item xs={6}>
                                    <TextInput fullWidth multiline source="address"/>
                                    <TextInput fullWidth source="phone_number"/>
                                    <TextInput fullWidth source="email"/>
                                </Grid>
                                <Grid item xs={12}>
                                    <TextInput fullWidth multiline source="description"/>
                                </Grid>
                            </Grid>
                        </FormTab>
                        <FormTab label="Page 2">
                            <Grid container spacing={3} style={{width: "100%"}}>
                                <Grid item xs={6}>
                                    <TextInput fullWidth source="languages"/>
                                    <AutocompleteArrayInput
                                        fullWidth
                                        source="tag"
                                        choices={choices}

                                    />
                                    <TextInput fullWidth source="title"/>
                                </Grid>
                                <Grid item xs={6}>
                                    <TextInput fullWidth source="years_of_experience"/>
                                    <TextInput fullWidth source="linkedin_id"/>
                                    <TextInput fullWidth source="skype_id"/>
                                </Grid>
                            </Grid>
                        </FormTab>
                        <FormTab label="Files">
                            <Grid container spacing={3} style={{width: "100%"}}>
                                <Grid item xs={6}>
                                    <ImageInput source="picture" accept="image/*">
                                        <ImageField source="src" title="title"/>
                                    </ImageInput>
                                </Grid>
                                <Grid item xs={6}>
                                    <ShowPicture picture={record.picture && record.picture}/>
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} style={{width: "100%"}}>
                                <Grid item xs={6}>
                                    <ImageInput source="picture_small" accept="image/*">
                                        <ImageField source="src" title="title"/>
                                    </ImageInput>
                                </Grid>
                                <Grid item xs={6}>
                                    <ShowPicture picture={record.picture && record.picture_small}/>
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} style={{width: "100%"}}>
                                <Grid item xs={6}>
                                    <FileInput source="cv" label="Related files" accept="application/pdf">
                                        <FileField source="src" title="title"/>
                                    </FileInput>
                                </Grid>
                                <Grid item xs={6}>
                                    <a href={record.picture && record.cv}>
                                        {record.picture && record.cv}
                                    </a>
                                </Grid>
                            </Grid>
                        </FormTab>
                        <FormTab label="Page 3">
                            <Box spacing={3} style={{width: "100%"}}>
                                <RichTextInput
                                    fullWidth
                                    multiline
                                    source="thanks_message"
                                    toolbar={[
                                        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                                        ['blockquote', 'code-block'],

                                        [{'header': 1}, {'header': 2}],               // custom button values
                                        [{'list': 'ordered'}, {'list': 'bullet'}],
                                        [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
                                        [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                                        [{'direction': 'rtl'}],                         // text direction

                                        [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                                        [{'header': [1, 2, 3, 4, 5, 6, false]}],

                                        [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                                        [{'font': []}],
                                        [{'align': []}],

                                        ['clean']                                         // remove formatting button
                                    ]}
                                />
                                <RichTextInput
                                    fullWidth
                                    multiline
                                    source="contact_title"
                                    toolbar={[
                                        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                                        ['blockquote', 'code-block'],

                                        [{'header': 1}, {'header': 2}],               // custom button values
                                        [{'list': 'ordered'}, {'list': 'bullet'}],
                                        [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
                                        [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                                        [{'direction': 'rtl'}],                         // text direction

                                        [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                                        [{'header': [1, 2, 3, 4, 5, 6, false]}],

                                        [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                                        [{'font': []}],
                                        [{'align': []}],

                                        ['clean']                                         // remove formatting button
                                    ]}
                                />
                                <RichTextInput
                                    fullWidth
                                    multiline
                                    source="contact_message"
                                    toolbar={[
                                        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                                        ['blockquote', 'code-block'],

                                        [{'header': 1}, {'header': 2}],               // custom button values
                                        [{'list': 'ordered'}, {'list': 'bullet'}],
                                        [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
                                        [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                                        [{'direction': 'rtl'}],                         // text direction

                                        [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                                        [{'header': [1, 2, 3, 4, 5, 6, false]}],

                                        [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                                        [{'font': []}],
                                        [{'align': []}],

                                        ['clean']                                         // remove formatting button
                                    ]}
                                />

                            </Box>
                        </FormTab>
                    </TabbedForm>
                </Edit>
            )
        }
    }
;


export default ProfileEdit;